import React, { Component } from 'react';
import TutorialEx from './components/TutorialComponent';
import Footer from './components/FooterComponent';
import './App.css';

class App extends Component {

  handleBackButtonClick(e) {

    const tag = document.querySelector('.App')
    tag.scrollIntoView();

  }

  render(){
    return (
      <div className="App" id="App">

        <button className="BackButton" onClick={this.handleBackButtonClick}>↑</button>

        <div className="Cover">
          <h1>Jaki laptop kupić?</h1>
          <p>Poradnik kupna laptopa</p>
        </div>

        <TutorialEx/>

        <Footer/>

      </div>)
    }
}

export default App;
