import React from 'react';
import Examples from './ExamplesComponent';
import './TutorialStyles.css';

const Tutorial = props => (
    <main className="Tutorial">
      <div className="Prolog">
        <p>Laptop (z ang. lap, podołek, + top, wierzch), notebook (z ang. notebook, notatnik, zeszyt) – rodzaj przenośnego komputera osobistego.
  
        Do podtypów laptopów można zaliczyć ultrabooki i netbooki.
        </p>
      </div>
  
      <Examples/>
  
    </main>)

export default Tutorial;